import React from 'react'
import Radium from 'radium'

import Title from '../common/Title'
import Card from '../common/Card'

class Header extends React.Component {
  render() {
    const containerStyles = {
      position: 'relative',
      margin: '0px',
      top: '27px',
      left: '118px',
      width: 'calc(100% - 118px)',
    }
    containerStyles['@media (max-width: 1439px)'] = {
      left: 118 * 0.9 + 'px',
      width: `calc(100% - ${118 * 0.9 + 'px'})`,
    }
    containerStyles['@media (max-width: 1279px)'] = {
      left: 118 * 0.8 + 'px',
      width: `calc(100% - ${118 * 0.8 + 'px'})`,
    }
    containerStyles['@media (max-width: 1139px)'] = {
      left: 118 * 0.7 + 'px',
      width: `calc(100% - ${118 * 0.7 + 'px'})`,
    }
    containerStyles['@media (max-width: 1023px)'] = {
      position: 'relative',
      boxSizing: 'border-box',
      width: '100%',
      top: '0px',
      left: '0px',
    }
    const subcontainerStyles = {
      width:'100%',
      position: 'relative',
      marginTop: '50px',
    }
    const cardStyles = {
      position: 'relative',
      display: 'inline-block',
      zIndex: '2',
      verticalAlign: 'middle',
      marginLeft: '0%',
      width: '35%',
    }
    cardStyles['@media (max-width: 1023px)'] = {
      width: '100%',
    }
    const titleStyles = {
      '@media (max-width: 1023px)': {
        padding: '15px 15px 0 15px',
      }
    }
    const imgStyles = {
      backgroundColor: 'transparent',
      position: 'relative',
      display: 'inline-block',
      verticalAlign: 'middle',
      zIndex: '1',
      marginLeft: '-20px',
      maxHeight: '462px',
      padding: '0px',
      width: '65%',
      maxWidth: '630px',
    }
    imgStyles['@media (max-width: 1023px)'] = {
      width: '100%',
      marginLeft: '0px',
    }

    let mobileAttributes = {}
    if (window.outerWidth < 1024) {
      mobileAttributes = {
        'no-ui': true,
        'padding': '15px',
      }
    }

    return (
      <div style={containerStyles}>
        <div style={titleStyles}>
          <Title size='h1' text='jerome bertier' />
        </div>
        <div style={subcontainerStyles}>
          <div style={cardStyles}>
            <Card
              width='100%'
              fontSize={14}
              title='Biographie'
              text={`Pianiste, Claveciniste, improvisateur, Jrme Bertier est diplm de la Hochschule de Freiburg et du CNSMD de Lyon. Il a tudi avec Michel Gaechter, Laurent Cabasso, Dirk Boerner et Robert Hill. Titulaire du premier master franais dimprovisation  en musique ancienne, il renoue sur scne avec cette pratique souvent dlaisse des interprtes. En soliste ou devant la toile du cin-concert, il parcourt les styles du XVIIme au XXIme sicle et du clavecin au piano, en passant par lorgue et le clavicorde. Passionn de musique de chambre, il se produit rgulirement avec les jeunes solistes de sa gnrations tels que Juliette Vialle, Clara Pouvreau,  Alexis Rousseau, Irne Martin et Brice Leclair.  En 2013 il cr le festival de musique de chambre du pays ajaccien, o ce vaste rpertoire sinstalle en plein coeur des villages de la rgion.  Intgr depuis 2016  la compagnie Volte Quarte de Chrystle Spinosi, il y partage la basse continue avec la gambiste Clara Fellmann dans une production autour de la musique du XVII sicle.
              <br>
              <br>En 2017 il enregistre sur France Musique dans lmission   limproviste  le rsultat de son travail avec le fltiste  bec Stlios Lazarou sur la musique traditionnelle ukrainienne. Il compose la musique du film  Plonger  de Juliette de Marcillac et enregistre la bande son du reportage  Jean Douchet, lenfant agit . Il se produit avec Alexis Rousseau et Raphal Moraly dans les Trio de  Ravel et de Beethoven (Cathdrale Sainte-Croix-des-Armniens, Paris) et cr les  visions japonaises  du compositeur Brice Leclair (Temple des Batignoles, Paris, avec Irne Martin et Raphal Moraly). Il obtient cette anne aussi le soutien de lADAMI.
              <br><br>
              Depuis 2018 Jrme Bertier collabore rgulirement avec lOrchestre Scession (dir. Clment Mao-Takacs) au Piano et au Celesta, et participe  ses actions pdagogiques et de mdiation (Abbaye de Royaumont avec Marianne Seleskovitch). Dans le cadre de la srie de concert  les musiciens au muse  (muse de la musique, philharmonie de Paris) il fait dcouvrir aux visiteurs loeuvre de Scarlatti, Couperin, DAnglebert et Bach sur les clavecins copis daprs Thibault et Goujon.
              <br><br>
              A la suite de ses tudes  la Hochschule de Freiburg, on le charge denseigner limprovisation aux lves de Bachelor, tous instruments et tous styles confondus. Depuis, il exprimente diverses formes denseignement de limprovisation notamment en vue du Certificat dAptitude quil prpare au CNSMDL. Il donne des masterclasses de piano et de musique ancienne.
              <br><br>
              Jrme Bertier prsente une confrence annuelle sur la musique grecque antique dans le cadre du sminaire dirig par Sandra Boehringer, maitresse de confrence en histoire antique  l'universit de Strasbourg. Il est lauteur de plusieurs travaux de recherche sur lacoustique musicale.
              `}
              {...mobileAttributes}
            />
          </div>
          <img style={imgStyles} src='/assets/piano.jpg' alt='piano' />
        </div>
      </div>
    )
  }
}

export default Radium(Header)



